import { DatePipe } from '@angular/common';
import { DistanciaPipe } from './../../pipes/distancia.pipe';
import { VelocidadePipe } from './../../pipes/velocidade.pipe';
import { SharedPipeModule } from './../../pipes/shred-pipe';
import { NgModule } from '@angular/core';
import { IonicPageModule } from 'ionic-angular';
import { RastroPage } from './rastro';
import { TranslateModule } from '@ngx-translate/core';

@NgModule({
  declarations: [
    RastroPage,
  ],
  imports: [
    IonicPageModule.forChild(RastroPage),
    TranslateModule.forChild(),
    SharedPipeModule
  ],
  providers: [
    DistanciaPipe, VelocidadePipe, DatePipe
  ]
})
export class RastroPageModule {}



