import { DatePipe } from '@angular/common';
import { DistanciaPipe } from './../../pipes/distancia.pipe';
import { VelocidadePipe } from './../../pipes/velocidade.pipe';
import { DataStore } from './../../models/data-store';
import { RastroGridPage, InspectionGridPage } from './../index';
import { VeiculoService } from './../../providers/api/veiculo.service';
import { Rastro } from './../../models/rastro';
import { Component, ViewChild, ElementRef } from "@angular/core";
import { normalizeURL} from 'ionic-angular';

import {
  IonicPage,
  NavController,
  NavParams,
  Platform,
  LoadingController
} from "ionic-angular";
import { TranslateService } from "@ngx-translate/core";
import {
  GoogleMap,
  GoogleMaps,
  GoogleMapsEvent,
  PolylineOptions,
  ILatLng,
  LatLng,
  Polyline,
  MarkerOptions,
  MarkerIcon,
  Marker,
  MapType,
  GoogleMapsMapTypeId,
  HtmlInfoWindow
} from "@ionic-native/google-maps";

@IonicPage()
@Component({
  selector: "page-rastro",
  templateUrl: "rastro.html"
})
export class RastroPage {
  @ViewChild("mapRastro") mapElement: ElementRef;

  public mapa: GoogleMap;
  waitString: string;
  directionString: string;
  dateTimeString: string;
  placaString: string;
  speedString: string;
  locationString: string;

  veiculo: DataStore;
  rastro: Rastro[] = [];
  inspecoes: any[] = [];
  tipoMapa: MapType = <MapType>GoogleMapsMapTypeId.NORMAL;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
    public translateService: TranslateService,
    private platform: Platform,
    private veiculoService: VeiculoService,
    private velocidadePipe: VelocidadePipe,
    private distanciaPipe: DistanciaPipe,
    private datePipe: DatePipe
  ) {
    this.veiculo = this.navParams.get("veiculo");
    this.rastro = this.navParams.get("rastro");
    this.inspecoes = this.navParams.get("inspecao");
    this.translateService.get(["WAIT", "DIRECTION", "DATETIME", "PLATE", "SPEED", "LOCATION"]).subscribe(values => {
      this.waitString = values["WAIT"];
      this.directionString = values["DIRECTION"];
      this.dateTimeString = values["DATETIME"];
      this.placaString = values["PLATE"];
      this.speedString = values["SPEED"];
      this.locationString = values["LOCATION"];
    });


  }

  ngOnInit() {
    this.platform.ready().then(() => {
      const points: ILatLng[] = [];
      const pointFocus: ILatLng[] = [];
      this.rastro.forEach(ponto => {
        points.push(new LatLng(ponto.latitude, ponto.longitude));
        pointFocus.push(new LatLng(ponto.latitude, ponto.longitude));
      });
      this.inspecoes.forEach(insp => {
        pointFocus.push(new LatLng(insp.lat, insp.lng));
      });
      let element = this.mapElement.nativeElement;
      if (pointFocus.length > 0) {
        this.mapa = GoogleMaps.create(element, {
          camera: {
            target: pointFocus
          }
        });
      } else {
        this.mapa = GoogleMaps.create(element);
      }

      this.mapa.one(GoogleMapsEvent.MAP_READY).then(() => {
        if (points.length > 0) {
          const polylineOptions: PolylineOptions = {
            points: points,
            color: '#17468A',
            width: 2,
            geodesic: true,
            clickable: true
          };
          this.mapa.addPolyline(polylineOptions).then((polyline: Polyline) => {
            let metaMarker: MarkerOptions = {
              position: {
                lat: this.rastro[0].latitude,
                lng: this.rastro[0].longitude
              },
              icon: {
                url: 'assets/icon/bandeira_inicio3.png'
              }
            };
            this.mapa.addMarker(metaMarker).then(mark => {

              mark.on(GoogleMapsEvent.MARKER_CLICK).subscribe(() => {
                let htmlInfoWindow = this.getWindowsInfo(this.rastro[0]);
                htmlInfoWindow.open(mark);
              });
            });;



            const ponto = this.rastro[this.rastro.length - 1];


            let path = "assets/icon/car.png";


            if (this.veiculo.iconType == 2){
              path = "assets/icon/custom/bus.png";
            }else if (this.veiculo.iconType == 3){
              path = "assets/icon/custom/forkfitft.png";
            }else if (this.veiculo.iconType == 4){
              path = "assets/icon/custom/ship.png";
            }else if (this.veiculo.iconType == 5){
              path = "assets/icon/custom/container.png";
            }else if (this.veiculo.iconType == 6){
              path = "assets/icon/custom/moto.png";
            }else {


              if (ponto.ignicao == true) {
                path = "assets/icon/car-ign.png";
              }else if(ponto.ignicao == false){
                path = "assets/icon/car.png";
              }
            }




            const iconVeiculo: MarkerIcon = {
              url: path,
              size: {
                width: 30,
                height: 30
              }
            };

            const direction = (ponto.direcao * 45);

            let metaMarker2: MarkerOptions = {
              position: {
                lat: ponto.latitude,
                lng: ponto.longitude
              },
              flat: true,
              rotation: direction,
              icon: iconVeiculo
            };
            this.mapa.addMarker(metaMarker2).then(mark => {

              mark.on(GoogleMapsEvent.MARKER_CLICK).subscribe(() => {
                let htmlInfoWindow = this.getWindowsInfo(ponto);
                htmlInfoWindow.open(mark);
              });
            });

            this.rastro.forEach((pontoSeta, index) => {

              if (index != 0 && index != this.rastro.length - 1) {
                let pathSeta = 'assets/icon/seta_off.png';
                if (pontoSeta.ignicao) {
                  pathSeta = 'assets/icon/seta_on.png';
                }


                const iconVeiculoSeta: MarkerIcon = {
                  url: pathSeta,
                  size: {
                    width: 30,
                    height: 30
                  }
                };


                const directionSeta = (pontoSeta.direcao * 45);
                let directionMarker: MarkerOptions = {
                  position: {
                    lat: pontoSeta.latitude,
                    lng: pontoSeta.longitude
                  },
                  flat: true,
                  rotation: directionSeta,
                  icon: iconVeiculoSeta,
                  anchor: [5, 0]
                };

                this.mapa.addMarker(directionMarker).then(mark => {

                  mark.on(GoogleMapsEvent.MARKER_CLICK).subscribe(() => {
                    let htmlInfoWindow = this.getWindowsInfo(pontoSeta);
                    htmlInfoWindow.open(mark);
                  });
                });
              }
            });

          });
        }


        if (this.inspecoes.length>0){
          this.inspecoes.forEach(ins=>{
            let metaMarkerInspe: MarkerOptions = {
              position: {
                lat: ins.lat,
                lng: ins.lng
              },
              flat: true,
              icon: 'assets/icon/icons8-inspecao-16.png'
            };
            this.mapa.addMarker(metaMarkerInspe).then(mark => {
              mark.on(GoogleMapsEvent.MARKER_CLICK).subscribe(() => {
                let htmlInfoWindow = this.getWindowsInfoInpe(ins);
                htmlInfoWindow.open(mark);
              });
            });
          });

        }
      });

    });
  }

  getWindowsInfo(pontoSeta: Rastro): HtmlInfoWindow {
    let dir = '';
    if (this.translateService.getBrowserLang() === 'pt' ||
      this.translateService.getBrowserLang() === 'pt-br') {
      switch (pontoSeta.direcao) {
        case 0: dir = 'Norte'; break;
        case 1: dir = 'Nordeste'; break;
        case 2: dir = 'Leste'; break;
        case 3: dir = 'Sudeste'; break;
        case 4: dir = 'Sul'; break;
        case 5: dir = 'Sudoeste'; break;
        case 6: dir = 'Oeste'; break;
        case 7: dir = 'Noroeste'; break;
      }

    } else {
      switch (pontoSeta.direcao) {
        case 0: dir = 'North'; break;
        case 1: dir = 'Northeast'; break;
        case 2: dir = 'East'; break;
        case 3: dir = 'Southeast'; break;
        case 4: dir = 'South'; break;
        case 5: dir = 'South-west'; break;
        case 6: dir = 'West'; break;
        case 7: dir = 'Northwest'; break;
      }
    }
    const velocidade = this.velocidadePipe.transform(pontoSeta.velocidade);
    const location = this.distanciaPipe.transform(pontoSeta.ptpDis) + pontoSeta.local;
    const dh = this.datePipe.transform(pontoSeta.dh, 'dd/MM/yyyy HH:mm:ss');
    let htmlInfoWindow = new HtmlInfoWindow();
    let frame: HTMLElement = document.createElement('div');
    frame.innerHTML = [
      '<div>' + this.directionString + ': ' + dir + '</div>',
      '<div>' + this.dateTimeString + ': ' + dh + '</div>',
      '<div>' + this.placaString + ': ' + pontoSeta.placa + '</div>',
      '<div>' + this.speedString + ': ' + velocidade + '</div>',
      '<div>Latitude: ' + pontoSeta.latitude.toFixed(5) + '</div>',
      '<div>Longitude: ' + pontoSeta.longitude.toFixed(5) + '</div>',
      '<div>' + this.locationString + ': ' + location + '</div>',
    ].join("");
    htmlInfoWindow.setContent(frame, {
      width: "250px",
      height: "150px"
    });
    return htmlInfoWindow;
  }


  getWindowsInfoInpe(pontoSeta: any): HtmlInfoWindow {
    const location = pontoSeta.posicao;
    const dh = this.datePipe.transform(pontoSeta.dh, 'dd/MM/yyyy HH:mm:ss');
    let htmlInfoWindow = new HtmlInfoWindow();
    let frame: HTMLElement = document.createElement('div');
    frame.innerHTML = [
      '<div><b>Cod: ' + pontoSeta.id + '</b></div>',
      '<div>' + this.dateTimeString + ': ' + dh + '</div>',
      '<div>Latitude: ' + pontoSeta.lat.toFixed(5) + '</div>',
      '<div>Longitude: ' + pontoSeta.lng.toFixed(5) + '</div>',
      '<div>' + location + '</div>',
    ].join("");
    htmlInfoWindow.setContent(frame, {
      width: "230px",
      height: "130px"
    });
    return htmlInfoWindow;
  }

  ionViewDidLoad() {
    console.log("ionViewDidLoad RastroPage");
  }


  openGrid() {
    this.veiculoService.tabRef
    .getByIndex(0).push(RastroGridPage, {
      veiculo: this.veiculo,
      rastro: this.rastro
    });
  }

  changeLayer(){

    if (this.tipoMapa == GoogleMapsMapTypeId.NORMAL){
      this.tipoMapa = <MapType>GoogleMapsMapTypeId.HYBRID;
    }else if(this.tipoMapa == GoogleMapsMapTypeId.HYBRID){
      this.tipoMapa = <MapType>GoogleMapsMapTypeId.SATELLITE;
    }else {
      this.tipoMapa = <MapType>GoogleMapsMapTypeId.NORMAL;
    }


    this.mapa.setMapTypeId(this.tipoMapa);
  }

  openInspection(){
    this.veiculoService.tabRef
    .getByIndex(0).push(InspectionGridPage, {
      veiculo: this.veiculo,
      inspecoes: this.inspecoes
    });
  }
}



